import { ReactLenis } from 'lenis/react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import Navbar from './Navbar';
import HeroSection from './HeroSection';
import InfoSection from './InfoSection';
import BenfitSection from './BenfitSection';
import TestimonialSection from './TestimonialSection';
import CounselingSection from './CounselingSection';
import BannerStudentImage from '../assets/tutor_web_student_banner.jpg';
import NameText from './name_text';
import Footer from './Footer';
import TermsAndConditions from './TermsAndConditions';
import LogoLoader from './LogoLoader';
import VaranasiLanding from './varanasi/varanasi_landing';

// Main App Component with Routing
export default function Index() {
  return (
    <Router>
      <ReactLenis root>
        <AppContent />
      </ReactLenis>
    </Router>
  );
}

// Separate component to handle routing and state


function AppContent() {
  const location = useLocation();
  const isVaranasi = location.pathname.startsWith('/varanasi');
  const activeTab = location.pathname.includes('/tutor') ? 'tutor' : location.pathname.includes('/student') ? 'student' : null;

  // Define meta tags based on route
  const metaData = {
    '/': {
      title: 'Tutor Kendra - Home Tutoring Services',
      description: 'Find top home tutors across India with Tutor Kendra.',
    },
    '/home': {
      title: 'Tutor Kendra - Home Tutoring Services',
      description: 'Personalized tutoring for students and opportunities for tutors.',
    },
    '/home/student': {
      title: 'Tutor Kendra - Find Tutors for Students',
      description: 'Discover expert tutors for personalized learning.',
    },
    '/home/tutor': {
      title: 'Tutor Kendra - Become a Tutor',
      description: 'Join Tutor Kendra as a tutor and teach students across India.',
    },
    '/varanasi': {
      title: 'Tutor Kendra - Home Tutors in Varanasi',
      description: 'Find the best home tutors in Varanasi for CBSE, ICSE, and UP Board students.',
    },
    '/varanasi/student': {
      title: 'Tutor Kendra - Varanasi Student Tutoring',
      description: 'Personalized home tutoring for students in Varanasi.',
    },
    '/varanasi/tutor': {
      title: 'Tutor Kendra - Become a Tutor in Varanasi',
      description: 'Join as a home tutor in Varanasi with flexible schedules.',
    },
    '/terms-and-conditions': {
      title: 'Tutor Kendra - Terms and Conditions',
      description: 'Read the terms and conditions for using Tutor Kendra services.',
    },
  };

  const currentMeta = metaData[location.pathname] || metaData['/'];

  return (
    <>
      <Helmet>
        <title>{currentMeta.title}</title>
        <meta name="description" content={currentMeta.description} />
        <meta property="og:title" content={currentMeta.title} />
        <meta property="og:description" content={currentMeta.description} />
        <meta property="og:url" content={`https://www.tutorkendra.in${location.pathname}`} />
      </Helmet>
      {/* Rest of the AppContent component unchanged */}
      <LogoLoader />
      {location.pathname !== '/terms-and-conditions' && (
        <>
          <Navbar activeTab={activeTab} showSwitch={!isVaranasi} />
          <main className="">
            {isVaranasi ? (
              <VaranasiLanding activeTab={activeTab || 'student'} />
            ) : (
              <>
                <div className="wrapper">
                  <HeroSection activeTab={activeTab || 'student'} />
                  <InfoSection activeTab={activeTab || 'student'} />
                </div>
                <TestimonialSection activeTab={activeTab || 'student'} />
                <BenfitSection activeTab={activeTab || 'student'} />
                <section className="py-8 flex justify-center items-center">
                  <img
                    src={BannerStudentImage}
                    alt="Student Banner"
                    className="w-[350px] md:w-[400px] lg:w-[600px] xl:w-[1200px] h-auto object-cover mx-8"
                  />
                </section>
                <CounselingSection activeTab={activeTab || 'student'} />
                <NameText />
                <Footer />
              </>
            )}
          </main>
        </>
      )}
      <Routes>
        <Route path="/" element={null} />
        <Route path="/home" element={null} />
        <Route path="/home/student" element={null} />
        <Route path="/home/tutor" element={null} />
        <Route path="/varanasi" element={null} />
        <Route path="/varanasi/student" element={null} />
        <Route path="/varanasi/tutor" element={null} />
        <Route path="/terms-and-conditions" element={<TermsAndConditions />} />
      </Routes>
    </>
  );
}