import { useNavigate, useLocation } from "react-router-dom";
import styled from "styled-components";
import logo from "../assets/logo2.png";
import telephone from "../assets/telephone.png";

const Navbar = ({ activeTab, showSwitch = true }) => {
  const navigate = useNavigate();
  const location = useLocation();

  // Determine the base path (/home or /varanasi)
  const basePath = location.pathname.startsWith("/varanasi") ? "/varanasi" : "/home";

  const handleTabChange = (tab) => {
    // Only navigate if the current path doesn't already match the target
    if (location.pathname !== `${basePath}/${tab}`) {
      navigate(`${basePath}/${tab}`);
    }
  };

  return (
    <StyledNav className="fixed top-0 left-0 right-0 text-black p-4 z-50 flex justify-between items-center mt-0 md:mt-0 mx-4 md:mx-10">
      <div className="h-12 w-36">
        <img src={logo} alt="Tutor Kendra Logo" />
      </div>

      <div className="items-center gap-4 flex flex-row flex-nowrap">
        <span className="text-xl font-bold items-center hidden lg:flex">
          <div className="py-[3px] -mr-10 relative">
            <img
              width="65"
              height="65"
              src={telephone}
              alt="phone"
            />
          </div>
          <a href="tel:+916388204164" className="bg-green-100 rounded-3xl py-4 px-10">
            +916388204164
          </a>
        </span>
        {showSwitch ? (
          <StyledWrapper>
            <div id="tabFilter" className="filter-switch">
              <input
                checked={activeTab === "student"}
                id="student"
                name="tabs"
                type="radio"
                onChange={() => handleTabChange("student")}
              />
              <label className="option" htmlFor="student">
                Student
              </label>
              <input
                checked={activeTab === "tutor"}
                id="tutor"
                name="tabs"
                type="radio"
                onChange={() => handleTabChange("tutor")}
              />
              <label className="option" htmlFor="tutor">
                Tutor
              </label>
              <span className="background" />
            </div>
          </StyledWrapper>
        ) : (
          <div className="w-[200px] h-[50px]"></div>
        )}
      </div>
    </StyledNav>
  );
};

const StyledNav = styled.nav`
  background: rgba(0, 0, 0, 0); /* Semi-transparent black for contrast */
  backdrop-filter: blur(5px); /* Blur effect */
  -webkit-backdrop-filter: blur(10px); /* Safari support */
  border-radius: 15px; /* Optional: rounded corners */
`;

const StyledWrapper = styled.div`
  .filter-switch {
    border: 2px solid #3674b5;
    border-radius: 30px;
    position: relative;
    display: flex;
    align-items: center;
    height: 50px;
    width: 200px;
    overflow: hidden;
  }
  .filter-switch input {
    display: none;
  }
  .filter-switch label {
    flex: 1;
    text-align: center;
    cursor: pointer;
    border: none;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
    z-index: 1;
    transition: all 0.5s;
    font-weight: 500;
    font-size: 16px;
  }
  .filter-switch .background {
    position: absolute;
    width: 49%;
    height: 38px;
    background-color: #3674b5;
    top: 4px;
    left: 4px;
    border-radius: 30px;
    transition: left 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  #tutor:checked ~ .background {
    left: 50%;
  }
  #student:checked + label[for="student"] {
    color: #ffffff;
    font-weight: bold;
  }
  #tutor:checked + label[for="tutor"] {
    color: #ffffff;
    font-weight: bold;
  }
  #student:not(:checked) + label[for="student"],
  #tutor:not(:checked) + label[for="tutor"] {
    color: #7d7d7d;
  }

  /* Responsive adjustments */
  @media (max-width: 640px) {
    .filter-switch {
      width: 150px;
      height: 40px;
    }
    .filter-switch label {
      font-size: 14px;
    }
    .filter-switch .background {
      height: 28px;
    }
  }
`;

export default Navbar;